import {useState, useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import { filmsLoad } from '../redux/actions';
import Typography from '@mui/material/Typography';
import MuiCard from '../components/MuiCard';
import './Main.scss';



export default function Main () {

const dispatch = useDispatch();

const [data, setData] = useState([]); //  ,    ,     
const [favor, setFavor] = useState([]); //   item

const sort = useSelector(state=>state.sort);//        ,       
const films= useSelector(state=>{return state.films;}) //   
    
    
    useEffect(()=>{dispatch(filmsLoad())}, []) //  
    
    useEffect(() => {if (films && films.length) {setData(films)}}, [films]) //     

    useEffect(() => {
          setFavor(data.filter((item)=>{return item.like===true}))}, [data]) //       

    useEffect(() => {}, [favor]) // , ..  setState    



    return(
    <div className='main'>
          <div className='main__container'>
            <Typography gutterBottom variant="h2" component="div">  </Typography>
          </div>
      <div className='main__cards'>
        {(sort===false?data:favor).map(item=>{//   ,      ,    ,   
          return <MuiCard 
            {...item} 
            arr = {data}
            methodSetState = {setData}//     ,   ,   .
            key={item.kinopoiskId}/>})}
      </div>
  </div>)
}
